* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.header {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    width: 50vw;
    margin: 2.5vh auto;
}

.header__button {
    background-color: #141414;
    color: #fff;
    border: 2px solid rgba(54, 54, 54, 0.6);
    font-weight: 600;
    position: relative;
    outline: none;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 45px;
    width: 130px;
    opacity: 1;
    transition: .3s;
}

.header__button:hover {
    color: #000000;
    background-color: #a7a7a7;
}

.header__link {
    text-decoration: none;
    color: #000;
    font-weight: bold;
    font-size: 30px;
}

#hamburger__menu {
    cursor: pointer;
    margin-left: 3.5vw;
    position: fixed;
}

.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    padding-top: 60px;
    transition: 0.5s;
}

.sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #fff;
    display: flex;
    transition: 0.3s;
}

.sidenav a img {
    width: 12.5%;
    margin-right: 5%;
}

.sidenav a:hover {
    color: #f1f1f1;
}

.sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
}

#main {
    transition: margin-left .5s;
    padding: 20px;
    margin-top: 2.5vh;
}

.main__square__view {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: space-evenly;
}

.single__article__square__view {
    width: 30vw;
    margin: 20px;
    height: 30vh;
}

.single__article {
    padding: 15px;
    text-align: center;
    max-width: 70vw;
    height: auto;
    margin: 5vh auto;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    box-shadow: 4px 4px 10px 0px rgba(66, 68, 90, 1);
}

.article__title {
    padding: 2.5vh 0;
    font-size: 20px;
    text-transform: uppercase;
}

.article__source {
    font-weight: bold;
}

.article__date {
    padding: 1vh 0;
}

.article__image {
    width: 85%;
    margin: 0 auto;
    padding: 2vh 0;
}


/* !! header popup */

.header__popup {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.85);
    display: flex;
    justify-content: center;
    align-items: center;
}

.header__popup .popup__inner {
    position: relative;
    padding: 25px 40px;
    width: 55vw;
    max-width: 640px;
    background-color: #fff;
    font-size: 24px;
    height: 35vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: justify;
}

.close__btn {
    position: absolute;
    top: 5px;
    right: 15px;
    outline: none;
    font-size: 50px;
    background: none;
    border: none;
    cursor: pointer;
}

.close__btn:hover {
    color: red;
}


/* !! ARTICLE popup */

.article__popup {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.15);
    display: none;
    /* flex */
    justify-content: center;
    align-items: center;
}

.article__popup__display {
    display: flex !important;
}

.article__popup__inner {
    position: relative;
    padding: 25px 30px;
    width: 75vw;
    max-width: 800px;
    background-color: #fff;
    font-size: 22px;
    height: 40vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    text-align: justify;
}

.article__popup__inner a {
    color: royalblue;
    font-weight: bold;
    font-size: 125%;
}

footer {
    margin: 5vh 0 0.75vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (max-height: 450px) {
    .sidenav {
        padding-top: 15px;
    }
    .sidenav a {
        font-size: 18px;
    }
}